import React, { forwardRef, memo } from 'react';
import cn from 'classnames';

import { ChipProps } from './Chip.types';

const RCN = 'thuiChip';

export const Chip = memo(
  forwardRef<HTMLInputElement, ChipProps>(
    (
      {
        title,
        kind = 'primary',
        size = 'md',
        className,
        iconLeft = null,
        iconRight = null,
        onIconLeftClick,
        onIconRightClick
      }: ChipProps,
      ref
    ) => {
      const chipCnString = cn(RCN, `${RCN}--${kind}`, `${RCN}--${size}`, className);

      return (
        <div className={chipCnString} ref={ref}>
          {iconLeft && (
            <div className={`${RCN}__icon-left`} onClick={onIconLeftClick}>
              {iconLeft}
            </div>
          )}
          {title}
          {iconRight && (
            <div className={`${RCN}__icon-right`} onClick={onIconRightClick}>
              {iconRight}
            </div>
          )}
        </div>
      );
    }
  )
);
